.header-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 100px;
  padding: 0 10px;
}

.home-link {
  font-size: 20px;
  color: #272727;
  text-decoration: none;
  font-weight: bold;
  -webkit-transition: opacity 0.3s ease-in-out;
  -moz-transition: opacity 0.3s ease-in-out;
  transition: opacity 0.3s ease-in-out;
}

.home-link:hover {
  opacity: 0.3;
}

.shopping-bag-icon {
  height: 30px;
  cursor: pointer;
  -webkit-transition: opacity 0.3s ease-in-out;
  -moz-transition: opacity 0.3s ease-in-out;
  transition: opacity 0.3s ease-in-out;
}

.shopping-bag-icon:hover {
  opacity: 0.3;
}
